body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.image {
  width:210px;
  height:140px;
}
h1 {
  text-align: center;
  margin-bottom: 30px;
}
.authoravatar {
  width:35px;
  height:33px;
  border-radius: 50%;
}
.writtenby {
  color: grey
}
div.posts-grid{
  margin-top: 10px;
  margin-bottom: 10px;
}
div.general-grid {
  /*border-top: 1px solid rgb(202, 202, 202);*/
  border-bottom: 1px solid rgb(202, 202, 202);
  align-items: center;
}
.votes {
  height: 10em;
  display: flex;
  align-items: center;
  justify-content: center }

.arrow {color: black }
.arrow:hover {
  cursor: pointer;
  color: orangered }
